.section {
	flex-basis: calc((100% - 40px) / 3);
}

.sectionTitle {
	margin-bottom: 10px;
}

.taskList {
	min-height: 100vh;
	padding: 10px;
	border-radius: 10px;
	background-color: #ced4da;
}

.taskItem {
	border: 1px solid #bbbbbb;
	border-radius: 5px;
	background-color: #ffffff;

	&:not(:last-child) {
		margin-bottom: 5px;
	}
}
